/* src/pages/styles/Page7_User_2.module.css */

/* --- START: 布局样式 (从 Page6 复制以保证一致) --- */
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  box-sizing: border-box;
  background-color: #f0f2f5;
  overflow: hidden;
  background-image: url('../../assets/背景带文字/Page7-User-2.svg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.leftPanel {
  width: 15%;
  height: 90%;
}

.rightPanel {
  width: 32%;
  height: 92%;
}

.mainContent {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  /* 添加以下属性确保内容真正居中 */
  margin: -100px auto;
  padding: 0 0; /* 添加内边距避免贴边 */
  bottom: 0%;
}
/* --- END: 布局样式 --- */


/* --- START: Page7 内部内容专属样式 --- */
/* (这些样式替换了 Page6 的 .cardCarousel, .arrowButton 等) */

/* 用于包裹单张卡片的容器，帮助实现居中 */
.cardDisplay {
  display: flex;
  justify-content: center;
  align-items: normal;
  flex-grow: 1; /* 占据主要空间，将卡片推到中间 */
  width: 100%;
  top: 20%;
  margin-top: 32vh;
}

/* 单张卡片的样式 */
.card {
  width: 40%;
  max-width: 460px;
}

.card img {
  width: 90%;
  height: auto;
  display: block;
  object-fit: contain;
}

/* 下一步按钮的样式 (可以从 Page6 的 .selectButton 复制并重命名) */
.nextButton {
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  margin-top: 2rem;
  transition: transform 0.2s ease-in-out, opacity 0.3s ease;
}

.nextButton:hover {
  transform: scale(1.05);
}

.nextButton svg {
  display: block;
}